.gradeCalculator {
    width: 70%;
    min-width: 285px;
    margin: 20px auto;
    padding: 20px 40px;
    border-radius: 20px;
    background: #D2FAFB;
}

.table {
    margin-top: 30px;
    max-width: 400px;
    margin: 0 auto;
}

.columnHeaders {
    display: flex;
}
.header {
    flex: 50%;
}

#averageButton {
    height: 35px;
    width: 140px;
    margin-top: 20px;
    font-size: 20px;
    border-radius: 20px;
    border: solid 3px black;
    background: #B5525C;
}